@import url(https://fonts.googleapis.com/css?family=Neuton&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-moz-selection {
  /* Code for Firefox */
  color: #24292e;
  background: #b1e77e;
}

::selection {
  color: #24292e;
  background: #b1e77e;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
}

body {
  /*background-image: linear-gradient(90deg, #08aeea, #2af598);*/
  background-image: url(/static/media/plant.10cd602e.jpg);
  background-size: 100%;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}
.color1 {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  height: 555px;
  width: 100%;
}

.box {
  width: 760px;
  text-align: center;
}

.box h1 {
  font-family: "Raleway", sans-serif;
  font-size: 52px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.15em;
  margin: 0;
  margin-right: 0px;
  margin-right: -0.15em;
  color: #555;
}

#full {
  font-family: "Raleway", sans-serif;
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 1em;
  margin: 1em 0 0;
  margin-right: 0px;
  margin-right: -1em;
  white-space: nowrap;
  color: #555;
}

#bio {
  font-family: "Neuton", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.6;
  margin: 3em 0 0;
  color: #777;
}
.color2 {
  position: absolute;
  -webkit-transform: translate(0px, 275px);
          transform: translate(0px, 275px);
  background-color: #fff;
  height: 75px;
  width: 250px;
  border-radius: 10px;
  border: solid 1px rgb(228, 228, 228);
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.01) 0px 5px 1px;
}
.git {
  margin-right: 25%;
  fill: #999999;
  height: 30px;
  width: 30px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.git:hover {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  fill: #24292e;
  margin-bottom: 5px;
}
.linkedin {
  fill: #999999;
  height: 30px;
  width: 30px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.linkedin:hover {
  -webkit-transition: 0.2s;
  transition: 0.2s;
  fill: #23500e;
  margin-bottom: 5px;
}

@media all and (display-mode: fullscreen) {
  .color1 {
    height: 666px;
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }
  .color2 {
    -webkit-transform: translate(0px, 325px);
            transform: translate(0px, 325px);
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }
}

